<template>
  <div id="loginPage">
    <div id="loginCreds">
      <h2>Log in</h2>
      <table>
        <tr>
          <td>Username: </td>
          <td>
            <input
            type="text"
            v-model="authUsername"
            placeholder="Username"/>
          </td>
        </tr>
        <tr>
          <td>Password: </td>
          <td style="text-align:left">
            <input
            type="password"
            v-model="authPassword"
            placeholder="Password"/>
          </td>
        </tr>
        <tr>
          <td>
            <router-link to="/login/forgotPassword">forgot password?</router-link>
          </td>
          <td style="text-align:right">
            <input
              type="submit"
              class="button"
              v-on:click="check"/>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      usernames: ['paurav','admin'],
      passwords: ['password','password'],
      emails: ['paurav@gmail.com','paurav.t20@gmail.com'],
      admins: ['paurav','admin'],
      mobilNo: ['9586666972','7567669881'],
      authUsername: "",
      authPassword: '',
    }
  },
  methods: {
    check: function() {
      if (this.usernames.includes(this.authUsername)) {
        const index = this.usernames.indexOf(this.authUsername);
        if (this.authPassword === this.passwords[index]) {
          localStorage.setItem('loggedIn','true');
          if(this.admins.includes(this.authUsername)){
            localStorage.setItem('isAdmin','true');
            this.$router.push('/admin');
          }
          else{
            console.log("inside here");
            localStorage.setItem('isAdmin','false');
            console.log(localStorage.getItem('loggedIn'));
            console.log(localStorage.getItem('isAdmin'));
            this.$router.push({name: 'Home'});
          }
          
        } else {
          alert('Password is incorrect');
        }
      } else {
        alert('This username does not exist');
      }
    }
  },
  mounted() {
    if(this.$route.query.name !== undefined) {
      this.usernames.push(this.$route.query.name);
      this.passwords.push(this.$route.query.password);
      this.emails.push(this.$route.query.email);
      this.mobilNo.push(this.$route.query.mobile);
    }
  },
}
</script>

<style scoped>
button{
  margin: 10px;
}

.alert{
    font-size: 10px;
    color: red;
}

*{
  margin: 0px;
}

input{
  border: 2px solid;
  border-radius: 4px;
  margin: 10px;
  padding: 2px;
}

.inputError {
  border: 2px solid #DE354C;
  border-radius: 4px;
}

.button{
  border: 2px solid #DE354C;
  background-color: #DE354C;
  color: #283747;
  border-radius: 5px;
  padding: 5px 15px;
  text-align: center;
  font-weight: bold;
  display: inline-block;
  font-size: 16px;
}

table, td, tr{
    font-weight: bold;
  background-color: #ffffff;
}

h2,h3{
  padding: 10px;
  margin-bottom: 20px;
  background: #ffffff;
}
#loginCreds{
  text-align: center;
  width: fit-content;
  float: center;
  margin: auto;
  padding: 20px;
  margin-top: 50px;
  background: #ffffff;
  border: 1px ;
  z-index: 100;
  box-shadow: 5px 7px 5px grey;
}
</style>